.home {
    width: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    font-size: 20px;
    background-color: #B4D6C1; 
    border: 2px solid #4C3D38; 
    padding: 30px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 8px 24px; 
}

.container > h1 {
    color: #2E2E2E; 
    margin-bottom: 20px;
}

.list {
    border: 1px solid #4C3D38; 
    background-color: #CBDAC6; 
    color: #2E2E2E; 
    padding: 10px;
    margin: 10px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 6px, rgba(0, 0, 0, 0.3) 0px 3px 6px;
}


.icon{
    transform: scale(1.5);
    margin-top: -3px;
}

/* Carousel */

.carousel{
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 6px, rgba(0, 0, 0, 0.3) 0px 3px 6px; 
}

.next:hover{
    transform: scale(1.1);
}

.next:active{
    transform: scale(0.9);
}

.next{
    border: 1px solid #2E2E2E;
    padding: 10px;
    border-radius: 100%;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 6px, rgba(0, 0, 0, 0.3) 0px 3px 6px;
    background-color: rgb(10, 21, 10);
    color: #c5e0b6;
}

/* Quote */

.quote{
    color: #464F41;
    padding: 10px;
    border: 5px solid white ;
    background-color: #BFCBA8;
    height: 100px;
    margin: 20px 5px;
    justify-items: center;
    font-size: large;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px;
}

.heading{
    color: #BFCBA8;
    margin: 20px 5px;
}

.quote-box{
    top : 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    background-color:#56776C;
    padding : 50px;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    width: 50%;
}

.quotebutton{
    background-color: beige;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

/* .quotebutton:hover{
    
} */

.quotebutton:active{
    opacity: 0.7;
    transform: scale(.9);
}


/* Accordian */

.btn:focus {
    outline: none;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
    
}

.title {
    background-color: #183218; 
    color: #fff; 
    width: 100%;
    /* height: 50px; */
    margin-top: 20px;
    padding: 10px;
    opacity: 0.9;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
}



.main {
    margin-top: 10px;
    justify-content: center;
    /* background-color: #547e54; */
    color: #000000;
    padding: 10px;
    opacity: 0.8;
    transition: transform .3s;
    transform: translateY(0);
}

.accbox {
    overflow: hidden;
    width: 50%;
    background-color: #aacfab; 
    top: 65%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    padding: 30px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}



/* todo */

.head{
    color:#c5e0b6;
}

.task{
    margin-bottom: 10px;
    padding: 5px;
    width: 40%;
}

.close{
    margin-top: -10px;
    transform: scale(1.5);
    /* margin-block-end; */
}

.editTask{
    padding: 10px;
    background-color: #c5e0b6;
    margin-top: 5px;
    border-left: 5px solid #464F41;
    width: 90%;
}

.taskName{
    text-align:start
}

.tododiv{
    width: 50%;
    background-color: rgb(0, 0, 0);
    top : 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    padding: 30px;
}

#strike{
    text-decoration: none;
}
